body {
    font-family: Arial, sans-serif;
    background-color: #e0f7fa;
}

.app-container {
    text-align: center;
    padding: 20px;
}

.main-content {
    display: flex;
    justify-content: space-between;
}

.canvas-container {
    flex: 1;
    position: relative;
    overflow: hidden;
}

.image-container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.user-drawing, .sample-drawing {
    width: 50%;
    max-width: 400px;
    border: 2px solid #007bff;
    padding: 10px;
    box-sizing: border-box;
    background-color: #f0f8ff;
}

.info-container {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.suggestion {
    margin-bottom: 20px;
    padding: 20px; /* Increased padding for larger appearance */
    border: 1px solid #007bff;
    background-color: #f0f8ff;
    border-radius: 5px;
}

.controls {
    display: flex;
    justify-content: center; /* Căn giữa các nút */
    margin-top: 30px; /* Khoảng cách trên giữa ô vẽ và các nút */
    margin-bottom: 10px; /* Khoảng cách dưới cùng giữa các nút và ô vẽ */
}

button {
    margin: 0 6px; /* Khoảng cách nhỏ giữa các nút */
    padding: 8px 12px; /* Kích thước cho nút */
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px; /* Bo góc */
    transition: background-color 0.3s;
    flex: none; /* Đảm bảo các nút không co giãn */
    width: 110px; /* Chiều rộng cố định cho nút */
    height: 40px; /* Chiều cao cố định cho nút */
}

button:hover {
    background-color: #0056b3; /* Màu nền khi hover */
}

.new-question {
    margin: 10px auto; /* Căn giữa */
    padding: 5px 10px; /* Kích thước nhỏ hơn */
}

.results {
    margin-top: 20px;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #007bff;
    color: white;
}

.brush-size-container {
    margin: 20px 12px; /* Khoảng cách trên và dưới */
    text-align: center; /* Căn giữa nội dung */
}

input[type="range"] {
    width: 80%; /* Đặt chiều rộng thanh trượt nhỏ hơn 100% */
    margin: 0 auto; /* Căn giữa thanh trượt */
    display: block; /* Đảm bảo thanh trượt là một khối */
}

.canvas-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}